#head{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    color: black;
}
#desdiv{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 20%;
}
#search{
display: flex;
justify-content: space-evenly;
align-items: center;
width: 20%;
}
#search>input{
    outline: none;
    background-color: whitesmoke;
    padding: 2% 3%;
    border-radius: 20px;
    font-size: 0.9rem;
    width: 110%;
}
#homecoming{
    color: rgba(0, 0, 0, 0.548);
    font-size: 3rem
}
#contentArea{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction:column ;
}
#medHolder{
    padding-top: 3%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    width:80%;
    margin-top: 0 auto;
}
 
#itemDetail{
    background: linear-gradient(60deg,wheat, whitesmoke);
    box-shadow: 0 0 5px black;
    padding: 2% 5%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    margin: 3% auto;
    border-radius: 10px 80px 10px 80px;
    color: black;
    filter: drop-shadow(3px 3px 2px whitesmoke);

}
#itemDetail:hover{
background-color: blanchedalmond;
box-shadow: 0 0 15px blue;
}
.shad{
    filter: drop-shadow(2px 2px 2px royalblue);
}
.shado{
    filter: drop-shadow(2px 2px 2px whitesmoke);
font-size: 2em;
letter-spacing: 3px;
}
#invNo{
    display: flex;
    width: 80%;
    background-color:red;
    justify-content: space-evenly;
    align-items: center;
}

.buttonClassbz {
    font-size:15px;
    font-family:Arial;
    width:140px;
    height:50px;
    border-width:1px;
    color:#3a8a9e;
    border-color:#d6bcd6;
    font-weight:bold;
    border-top-left-radius:15px;
    border-top-right-radius:15px;
    border-bottom-left-radius:15px;
    border-bottom-right-radius:15px;
    box-shadow: 3px 4px 0px 0px #899599;
    text-shadow: 0px 1px 0px #e1e2ed;
    background:linear-gradient(#ededed, #bab1ba);
    padding: unset;
  }
  
  .buttonClassbz:hover {
    background: linear-gradient(#bab1ba, #ededed);
  }
