  #additem{
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      align-items: center;
      padding: 0 3%;
      box-shadow: 0px 0px 5px black;
      background-color: whitesmoke;
      z-index: 5;
      position: absolute;
      left: 55%;
      top: 35%;
      display: none;
      padding-bottom: 1%;
      box-sizing: border-box;
      filter: drop-shadow(5px 5px 2px black);
      background: linear-gradient(60deg,rgb(241, 11, 11), whitesmoke);
    border-radius: 80px 20px 80px 20px;
  }
  .ws,.fa-window-close{
    filter: drop-shadow(2px 2px 2px rgb(19, 108, 209));

  }
  .addform{
      border: none;
      outline: none;
      filter: drop-shadow(2px 2px 2px white);
      margin-bottom: 5%;
      background-color: transparent;
      padding-bottom: 2%;
      color: black;
      border-bottom: 2px solid black;
  }
  #btn{margin-top: 20%;}
  #cancel{
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 2%;
      width: 100%;
  }
  #cancel>i{
      cursor: pointer;
  }