#maindiv {
    width: 100%;
    margin-top: unset;
  }
  #crudordnav {
    border-radius: 20px;
    padding:  0% 2%;
    background:linear-gradient(45deg,#E55246,#e62614);
    display: flex;
    justify-content: space-between;
    align-self: center;
    box-shadow: 2px 2px 5px black;
  }
  #desdiv {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 20%;
  }
  #homecoming {
    color: rgba(0, 0, 0, 0.548);
    font-size: 3rem;
  }
  #formDiv{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    padding: 2% 0;
    width: 40%;
    margin: 5% auto;
    box-shadow:3px 3px 5px black;
    background: linear-gradient(60deg,wheat, whitesmoke);
    border-radius: 10px;
  }
  #form2{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 60vh;
    width: 50%;
  }
#stock,#total{
    width: 80%;
    height: 7vh;
    font-size: 2rem;
    border: none;
    outline: none;
    border-bottom: 3px solid black;
    background-color: transparent;
    }

  #buttonDiving{
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 70%;
      margin: unset;
      margin-top: 5%;
  }
  .buttonClass7{
    font-size:15px;
    font-family:Arial;
    width:140px;
    height:50px;
    border-width:1px;
    color:#3a8a9e;
    border-color:#d6bcd6;
    font-weight:bold;
    border-top-left-radius:15px;
    border-top-right-radius:15px;
    border-bottom-left-radius:15px;
    border-bottom-right-radius:15px;
    box-shadow: 3px 4px 0px 0px #899599;
    text-shadow: 0px 1px 0px #e1e2ed;
    background:linear-gradient(#ededed, #bab1ba);
    padding: unset;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .buttonClass7:hover {
    background: linear-gradient(#bab1ba, #ededed);
  }
  #productList{
      font-size: 1.5rem;
      padding: 5% 0%;
      width:50%;
      text-align: center;
  }
  #addMed{
    width: 90%;
    height: 70vh;
    background-color: whitesmoke;
    top: 25vh;
    left:5%;
    position: fixed;

    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    display: none;
    }
    #medhold{
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      width: 90%;
      background:linear-gradient(40deg,black,rgb(88, 88, 88));
      padding: 10px;
     margin: 0 auto;
     margin-top: 5px;
     box-shadow: 5px 3px 3px black;

    }
    #hold{
      width: 100%;
      overflow-y: auto;
      overflow-x: hidden;
    }
    #minus,#plus{
      cursor: pointer;
    }
    .edit{
      color: black;
      filter: drop-shadow(2px 2px 3px yellow);
    }
    
    #ordset{
      padding: 2%;
      max-height: 30vh;
      overflow-x: hidden;
      overflow-y: auto;
      width:80%;
      box-shadow: 2px 2px 9px black;
      background: linear-gradient(70deg,red,whitesmoke);
      border-radius: 20px;
    }
    #Nameing,#mobile{
      font-size:1.4rem;
      font-weight: 600;
      letter-spacing: 2px; 
    }
    .medsha{
      filter: drop-shadow( 2px 2px 1px white);
    }
    .fa-plus,.fa-minus{
      color: red;
      font-size: 1.5rem;
      filter: drop-shadow(2px 2px 1px white);
    }
    .medshadow{
      color: whitesmoke;
     filter: drop-shadow(2px 2px 2px red);
    }