#maindiv{
    width: 100%;
    height: 100vh;
}
#navbaring{
    border-radius: 20px;
    padding:  0% 2%;
    background:linear-gradient(45deg,#E55246,#e62614);
    display: flex;
    justify-content: space-between;
    align-self: center;
    border-bottom: 3px solid #3E4095;
}
#desdiv{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 20%;
}
#homecoming{
    color: rgba(0, 0, 0, 0.548);
    font-size: 3rem;
    filter: drop-shadow(3px 3px 2px white);

}
#contentDiv{
    padding-top: 2%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    background:linear-gradient(60deg,cadetblue, skyblue, cadetblue);
}
.ml{margin-left: 10px;}
#manageInventory,#manageOrders,#ManageTeam{
    box-shadow: 0px 0px 8px #3E4095;
    width:30%;
    height: 80vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    background:linear-gradient(45deg,rgb(235, 102, 102), #E55246);
    border-radius: 30px;
    border-radius: 100px 0px 100px 0px  
    

}
#inventory{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
}
#Team{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
}
#TeamDetails{
    padding-top: 5%;
    width: 50%;
    display: flex;
    flex-direction: column ;
    justify-content: space-evenly;
    align-items: center;
    box-shadow: 0px 0px 5px #3E4095;
    background: linear-gradient(60deg,wheat, whitesmoke);
    font-size: 1rem;
    border-radius: 10px 80px 10px 80px;
    margin-top: 2%;
}
#orders{
    width: 100%;
    display: flex;
    justify-content: center;
  
    align-items: center;
    flex-wrap: wrap;
}
#orderdetails{
    width: 80%;
    padding: 4%  6%;    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    box-shadow: 0px 0px 5px #3E4095;
    border-radius: 30px 120px 30px 120px;
    background: linear-gradient(60deg,wheat, whitesmoke);

}
.buttonClass {
    font-size:15px;
    font-family:Arial;
    width:140px;
    height:50px;
    border-width:1px;
    color:#3a8a9e;
    border-color:#d6bcd6;
    font-weight:bold;
    border-radius:15px;
    box-shadow: 3px 4px 0px 0px #899599;
    text-shadow: 0px 1px 0px #e1e2ed;
    background:linear-gradient(#ededed, #bab1ba);
    text-align: center;
    padding: unset;
  }
  
  .buttonClass:hover {
    background: linear-gradient(#bab1ba, #ededed);
  }
  #meddetails{
    width:40%;
    box-shadow: 0px 0px 8px #3E4095;
    border-radius: 10px 50px 10px 50px;
    background: linear-gradient(60deg,wheat, whitesmoke);
    margin: 2% 0;
    text-align: center;
  }
  #logouting{

        font-size: 2rem;
        align-items: center;
        filter:drop-shadow( 0 0 5px black);
        position: absolute;
        left:97%;
        top: 1%;
        font-weight: 600;
    
  }
  #logo{
      display: flex;
      justify-content: center;
      align-items: center;
      font-family: cursive;
      letter-spacing: 3px;
      font-size: 4rem;
      color: cadetblue;
      filter: drop-shadow(3px 3px 2px whitesmoke);
  }
  #nameed,#welcome,#des{
      color: wheat;
    filter: drop-shadow(3px 3px 2px black);
  }
  .manashadow{
    filter: drop-shadow(3px 3px 2px whitesmoke);
    color: maroon;
  }