#maindiv {
    width: 100%;
    margin-top: unset;
    padding-bottom: 5%;
  }
  #manageordnav{
    border-radius: 20px;
    padding:  0% 2%;
    background:linear-gradient(45deg,#E55246,#e62614);
    display: flex;
    justify-content: space-between;
    align-self: center;
    box-shadow: 2px 2px 5px black;
  }
  #desdiv {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 20%;
  }
  #homecoming {
    color: rgba(0, 0, 0, 0.548);
    font-size: 3rem;
  }
  #content {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
  }
  #ordercontainer1{
      width:80%;
      margin:0 auto;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-evenly;
      align-items: center;
  }
  #orderDetailing{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items:center;
  width:30%;
  box-shadow: 3px 2px 5px black;
  border-radius: 20px;
  padding: 0;
  margin-top: 3%;
  background: linear-gradient(40deg,red, white);
  
  
  }
  #orderDetail:hover {
      background-color: blanchedalmond;
  box-shadow: 2px 3px 5px black;
  
    }
  #item{
    font-size: 1.3rem;
  }
  .buttonClass3{
    font-size:15px;
    font-family:Arial;
    width:140px;
    height:50px;
    border-width:1px;
    color:#3a8a9e;
    border-color:#d6bcd6;
    font-weight:bold;
    border-top-left-radius:15px;
    border-top-right-radius:15px;
    border-bottom-left-radius:15px;
    border-bottom-right-radius:15px;
    box-shadow: 3px 4px 0px 0px #899599;
    text-shadow: 0px 1px 0px #e1e2ed;
    background:linear-gradient(#ededed, #bab1ba);
    padding: unset;
  }
  a{
    text-align: center;
  }
  .buttonClass3:hover {
    background: linear-gradient(#bab1ba, #ededed);
  }
  #medHold{
    width: 80%;
    color: black;
    font-size: 0.7rem;
    max-width: 35vh;
    overflow-y: auto;
    overflow-x: hidden;
  }
  #product{
    font-weight: 600;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1rem;
    padding: 2%;
  }
  #logout{
    font-size: 2rem;
    align-items: center;
    filter:drop-shadow( 0 0 5px black);
    position: absolute;
    left:97%;
    top: 1%;
    font-weight: 600;
}
#exesha{
  letter-spacing: 3px;
  font-size: 2rem;
  filter: drop-shadow(2px 2px 1px white);

}
.detsha{
  filter: drop-shadow(2px 2px 1px white);
}