#maindiv {
  width: 100%;
  margin-top: unset;
}
#editTeamNav{
  border-radius: 20px;
    padding:  0% 2%;
    background:linear-gradient(45deg,#E55246,#e62614);
  display: flex;
  justify-content: space-between;
  align-self: center;
  box-shadow: 5px 5px 10px black;
}
#desdiv {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 20%;
}
#homecoming {
  color: rgba(0, 0, 0, 0.548);
  font-size: 3rem;
}
#sha{
  filter: drop-shadow(2px 2px 3px white);
  letter-spacing: 3px;
  font-size: 2rem;
}
#teamImg > img {
  width: 100%;
  height: 50%;
}
#firstName {
  margin-right: 5%;
}
#contentArea {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
}
#TeamHolder {
  padding-top: 3%;
  width: 80%;
  margin-top: 0 auto;
  display:flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;

}

#TeamDetail {
  background: linear-gradient(60deg,wheat, whitesmoke);
  box-shadow: 2px 2px 5px black;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  margin: 3% auto;
  border-radius: 20px;
  overflow: hidden;
}
.sha1{
  color:black;
  filter: drop-shadow(2px 2px 3px black);
}
#ds{
  filter: drop-shadow(2px 2px 1px white);
}
a{
  width: 30%;
}
#memdetail {
text-align: left;
margin: 0 auto;
}
#TeamDetail:hover {
  background-color: blanchedalmond;
  box-shadow: 0 0 5px black;
}

.buttonClassaz {
  font-size: 15px;
  font-family: Arial;
  width: 140px;
  height: 50px;
  border-width: 1px;
  color: #3a8a9e;
  border-color: #d6bcd6;
  font-weight: bold;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  box-shadow: 3px 4px 0px 0px #899599;
  text-shadow: 0px 1px 0px #e1e2ed;
  background: linear-gradient(#ededed, #bab1ba);
  padding: unset;
}

.buttonClassaz:hover {
  background: linear-gradient(#bab1ba, #ededed);
}
.buttonClass2{
  font-size: 15px;
  font-family: Arial;
  width: 140px;
  height: 40px;
  border-width: 1px;
  color: #3a8a9e;
  border-color: #d6bcd6;
  font-weight: bold;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  box-shadow: 3px 4px 0px 0px #899599;
  text-shadow: 0px 1px 0px #e1e2ed;
  background: linear-gradient(#ededed, #bab1ba);
  padding: unset;

}

.buttonClass2:hover {
  background: linear-gradient(#bab1ba, #ededed);
}
#addMember {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 0 3%;
  box-shadow: 0px 0px 5px black;
  background-color: whitesmoke;
  z-index: 5;
  position: absolute;
  left: 55%;
  top: 35%;
  display: none;
  padding-bottom: 1%;
  box-sizing: border-box;
  filter: drop-shadow(5px 5px 2px black);
  background: linear-gradient(60deg,rgb(241, 11, 11), whitesmoke);
border-radius: 80px 20px 80px 20px;
}
.addform {
  border: none;
  outline: none;
  filter: drop-shadow(2px 2px 2px white);
  background-color: transparent;
  padding-bottom: 2%;
  color: black;
  border-bottom: 2px solid black;
  margin-bottom: 5%;
}
#btn2{
  margin-top: 10%;
}
#canceled{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2%;
  width: 100%;
}
#canceled>i{
  cursor: pointer;
  font-size: 1.5rem;
}