#maindiv {
    width: 100%;
    margin-top: unset;
  }
  #teammemnav {
    border-radius: 20px;
    padding:  0% 2%;
    background:linear-gradient(45deg,#E55246,#e62614);
    display: flex;
    justify-content: space-between;
    align-self: center;
    box-shadow: 0 0 5px black;
  }
  #desdiv {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 20%;
  }
  #homecoming {
    color: rgba(0, 0, 0, 0.548);
    font-size: 3rem;
  }
  #formdivi{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    padding: 2% 0;
    width: 40%;
    margin: 5% auto;
    box-shadow:0 0 5px black;
    background: linear-gradient(60deg,wheat, whitesmoke);
   border-radius:50px 150px 50px 150px;
  }
  #form3{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 60vh;
    width: 50%;
  }
#stock,#medName,#mafName{
    width: 80%;
    height: 7vh;
    font-size: 2rem;
    border: none;
    outline: none;
    border-bottom: 3px solid black;
    background-color: transparent;
    }
  
  #buttonDiv{
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      width: 70%;
  }
  .mngmed2{
    filter: drop-shadow(2px 2px 2px black);
    font-size: 1.2rem;
    font-weight: 500;
  }
  .buttonClass4{
    font-size:15px;
    font-family:Arial;
    width:140px;
    height:50px;
    border-width:1px;
    color:#3a8a9e;
    border-color:#d6bcd6;
    font-weight:bold;
    border-top-left-radius:15px;
    border-top-right-radius:15px;
    border-bottom-left-radius:15px;
    border-bottom-right-radius:15px;
    box-shadow: 3px 4px 0px 0px #899599;
    text-shadow: 0px 1px 0px #e1e2ed;
    background:linear-gradient(#ededed, #bab1ba);
  }
  
  .buttonClass4:hover {
    background: linear-gradient(#bab1ba, #ededed);
  }
  #sName,#fname{margin-left: 3%;}
.widthadjust{
  display: block;
    width: 100%;
    text-align: center;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }
  .widthadjust>span{
    font-size: 1.5rem;
    font-weight: 600;
  }